import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.primary.light,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    title: {
        color: "white"
    },
    button : {
        marginRight: theme.spacing(4),
        fontSize: '2rem'
    },
    flex: {
        display: "flex",
        flexDirection: "column",

    },
    sideflex: {
        display: "flex",
        justifyContent: "space-around",
    },
    size: {
        fontSize: "1rem",
        padding: 0
    },
    num : {
        borderWidth: 1,
        borderStyle: 'solid',
        borderColor: 'theme.palette.primary.dark',
        borderRadius: 3,
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        height: 48,
        padding: '0 30px',
        fontSize: '1.5rem',
        backgroundColor: theme.palette.grey["100"],
        marginBottom: theme.spacing(1.5)
    }
}));

export default function CardHook(props) {

    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);


    return (
        <Card className={classes.root}>
            <CardHeader className={classes.head}
                title={
                    <Typography variant="h5" className={classes.title}>
                        {props.elem.title}
                    </Typography>
                }
            />
            <CardContent className={classes.sideflex}>
                <div className={classes.flex}>
                    <Button className={classes.num}>
                        {props.elem.yesCount +  props.elem.noCount}
                    </Button>
                    <Typography variant="h6">
                        Mails Sent
                    </Typography>
                </div>
                <div className={classes.flex}>
                    <Button className={classes.num}>
                        0
                    </Button>
                    <Typography variant="h6">
                        Pending
                    </Typography>
                </div>
                <div className={classes.flex}>
                    <Button className={classes.num}>
                        {props.elem.yesCount}
                    </Button>
                    <Typography variant="h6">
                        Yes Count
                    </Typography>
                </div>
                <div className={classes.flex}>
                    <Button className={classes.num}>
                        {props.elem.noCount}
                    </Button>
                    <Typography variant="h6">
                        No Count
                    </Typography>
                </div>
            </CardContent>
            <CardActions disableSpacing>
                <IconButton aria-label="add to favorites">
                    <FavoriteIcon />
                </IconButton>
                <IconButton aria-label="share">
                    <ShareIcon />
                </IconButton>
                <IconButton
                    className={clsx(classes.expand, {
                        [classes.expandOpen]: expanded,
                    })}
                    aria-expanded={expanded}
                    aria-label="show more"
                >
                    <Button variant="contained" color="secondary" onClick={props.onDeleteClick.bind(this,props.elem.id)}>
                        Delete
                    </Button>
                </IconButton>
            </CardActions>
        </Card>
    );
}