import React, {Component} from 'react';
import DashboardHook from "../hooks/DashboardHook";
import {connect} from "react-redux";
import {deleteSurvey, getSurveys} from "../actions/surveyActions";

class DashBoard extends Component {
    componentDidMount() {
        this.props.getSurveys();
    }

    onDeleteClick = id => {
        this.props.deleteSurvey(id);
    }

    render() {
        const {surveys} = this.props.surveys;
        const {credits} = this.props.security;
        return (
            <DashboardHook surveys={surveys} onDeleteClick={this.onDeleteClick} credits={credits}/>
        );
    }
}
const mapStateToProps = state => ({
    surveys: state.surveys,
    security: state.security
})

export default connect(mapStateToProps,{getSurveys , deleteSurvey})(DashBoard);