import React, {Component} from 'react';
import {connect} from "react-redux";
import {logout, updateCredits} from "../../actions/securityActions";
import HeaderHookAuthenticated from "../../hooks/HeaderHookAuthenticated";
import HeaderHookNotAuthenticated from "../../hooks/HeaderHookNotAuthenticated";

class Header extends Component {
    constructor() {
        super();
        this.updateMyCredits = this.updateMyCredits.bind(this);
        this.logout = this.logout.bind(this);
    }
    logout() {
        this.props.logout();
        window.location.href = "/";
    }

    updateMyCredits(token) {
        const id = this.props.security.user.id;
        this.props.updateCredits(id,token);
    }

    render() {
        const {validToken, user, credits} = this.props.security;
        if(validToken && user) {
            return (
                <HeaderHookAuthenticated user={user.fullname} logout={this.logout} credits={credits} updateMyCredits={this.updateMyCredits}/>
            );
        } else {
            return (
                <HeaderHookNotAuthenticated />
            );
        }
    }
}

const mapStateToProps = state => ({
    security: state.security
})

export default connect(mapStateToProps, {logout, updateCredits})(Header);