import {DELETE_SURVEY, GET_SURVEYS} from "../actions/types";

const initialState ={
    surveys: []
}

export default function (state=initialState,action) {
    switch (action.type) {
        case GET_SURVEYS:
            return {
                ...state,
                surveys: action.payload
            }
        case DELETE_SURVEY:
            return {
                ...state,
                surveys: state.surveys.filter(survey => survey.id != action.payload)
            }
        default:
            return state;
    }

}