import React, {Component} from 'react';
import StripeCheckout from 'react-stripe-checkout';
import Button from "@material-ui/core/Button";

class Payments extends Component {

    constructor() {
        super();
        this.tokenCallback = this.tokenCallback.bind(this);
    }
    tokenCallback(token) {
        this.props.updateMyCredits(token);
    }

    render() {
        return (
            <StripeCheckout
                amount={500}
                token={ token => this.tokenCallback(token)}
                stripeKey={process.env.REACT_APP_STRIPE_KEY}
                name="uservey"
                description="$5 for 5 email credit"
            >
                <Button variant="contained" color="secondary" style={{"marginRight": "2rem"}}>
                    Add Credits
                </Button>
            </StripeCheckout>
        );
    }
}

export default Payments;