import axios from "axios";
import {DELETE_SURVEY, GET_SURVEYS} from "./types";

export const createSurvey = (survey,history) => async dispatch => {
        await axios.post("/sendgrid/send", survey);
        history.push("/dashboard");
};

export const getSurveys = () => async dispatch => {
        const res = await axios.get("/sendgrid/all");
        dispatch({
                type: GET_SURVEYS,
                payload: res.data
        })
}

export const deleteSurvey = id => async dispatch => {
        await axios.delete(`/sendgrid/${id}`);
        dispatch({
                type: DELETE_SURVEY,
                payload: id
        });
}