import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";
import CardHook from "./CardHook";
import Fab from "@material-ui/core/Fab";
import AddIcon from '@material-ui/icons/Add';
import Typography from "@material-ui/core/Typography";
import {Link} from "react-router-dom";
import PopupHook from "./PopupHook";


const useStyles = makeStyles((theme) => ({
    layout: {
        width: 'auto',
        height: '100vh'
    },
    flex: {
        display: "flex",
        flexDirection: "column"
    },
    margin : {
        marginTop: theme.spacing(3),
        marginBottom: theme.spacing(2),
    },
    fab: {
        margin: 0,
        top: 'auto',
        right: theme.spacing(10),
        bottom: theme.spacing(10),
        left: 'auto',
        position: 'fixed',
    },
    fabtext: {
        fontSize: theme.spacing(2),
    }

}));

function DashboardHook(props) {
    const classes = useStyles();
    let button;
    if(props.credits > 0) {
        button = (
            <Fab  component={Link} to={"/addSurvey"} size="large" variant="round" color="secondary" aria-label="add" className={classes.fab}>
                <AddIcon />
            </Fab>
        );
    } else {
        button = <PopupHook />
    }
    return (
        <div>
            {button}
            <Grid container justify="center" className={classes.layout}>
                <Grid item xs={0} md={2} lg={3}>
                </Grid>
                <Grid item xs={12} md={8} lg={6}>
                    <div className={classes.flex}>
                        {props.surveys.map(elem => (
                            <div className={classes.margin}>
                                <CardHook elem={elem} onDeleteClick={props.onDeleteClick}/>
                            </div>
                        ))}
                    </div>

                </Grid>
                <Grid item xs={0} md={2} lg={3}>
                </Grid>
            </Grid>
        </div>
    );
}

export default DashboardHook;