import React, {Component} from 'react';
import {createSurvey} from "../actions/surveyActions";
import {connect} from "react-redux";
import AddSurveyHook from "../hooks/AddSurveyHook";
import {reduceOneCredit} from "../actions/securityActions";

class AddSurvey extends Component {
    constructor() {
        super();

        this.state = {
            subject : "",
            title: "",
            recipientEmails: "",
            content : ""
        }
        this.onSubmit = this.onSubmit.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onSubmit(e) {
        e.preventDefault();
        const id = this.props.security.user.id;
        this.props.reduceOneCredit(id);
        const newSurvey = {
            subject : this.state.subject,
            title: this.state.title,
            recipientEmails: this.state.recipientEmails,
            content : this.state.content
        }
        this.props.createSurvey(newSurvey,this.props.history);
    }


    render() {
        return (
            <AddSurveyHook onSubmit={this.onSubmit} onChange={this.onChange} subject={this.state.subject} title={this.state.title} recipientEmails={this.state.recipientEmails} content={this.state.content} />
        );
    }
}

const mapStateToProps = state => ({
    security: state.security
})


export default connect(mapStateToProps,{createSurvey,reduceOneCredit})(AddSurvey);