import React from 'react';
import {makeStyles} from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import CssBaseline from "@material-ui/core/CssBaseline";
import Avatar from "@material-ui/core/Avatar";
import ListAltIcon from '@material-ui/icons/ListAlt';
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(10),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(2),
        backgroundColor: theme.palette.primary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(4, 0, 4),
    },
    heading: {
        fontSize: theme.spacing(5.4)
    },
}));

function AddSurveyHook(props) {
    const classes = useStyles();
    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <ListAltIcon />
                </Avatar>
                <Typography variant="h5" className={classes.heading}>
                    Survey Form
                </Typography>
                <form className={classes.form} noValidate onSubmit={props.onSubmit}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} >
                            <TextField name="subject" variant="outlined" required fullWidth id="subject" label="Mail Subject" autoFocus value={props.subject} onChange={props.onChange}/>
                        </Grid>
                        <Grid item xs={12} >
                            <TextField name="title" variant="outlined" required fullWidth id="title" label="Survey Title" autoFocus value={props.title} onChange={props.onChange}/>
                        </Grid>
                        <Grid item xs={12} >
                            <TextField name="recipientEmails" variant="outlined" required fullWidth id="recipientEmails" label="Recipient Emails (Comma Seperated)" autoFocus value={props.recipientEmails} onChange={props.onChange}/>
                        </Grid>
                        <Grid item xs={12} >
                            <TextField name="content" variant="outlined" required fullWidth id="content" label="Mail Content" autoFocus value={props.content} onChange={props.onChange}/>
                        </Grid>
                    </Grid>
                    <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>
                        Create Survey
                    </Button>
                </form>
            </div>
        </Container>
    );
}

export default AddSurveyHook;