import {SET_CURRENT_USER, UPDATE_CREDITS} from "../actions/types";

const initialState = {
    user: {},
    validToken: false,
    credits: 0
}

const booleanActionPayload = (payload) => {
    if(payload)
        return true;
    return false;
}

export default function (state=initialState,action) {
    switch (action.type) {
        case SET_CURRENT_USER:
            return {
                ...state,
                validToken: booleanActionPayload(action.payload),
                user: action.payload,
                credits: action.payload.credits
            }
        case UPDATE_CREDITS:
            return {
                ...state,
                credits: action.payload
            }
        default:
            return state
    }
}